.website-breadcrumbs {
    &__item {
        font-family: 'Merriweather';
    
        font-size: 18px;
        line-height: 27px;
    
        font-weight: 400;
        font-style: normal;
    
        text-align: center;
    }

    &__item--previous::after {
        content: "/";
        margin-left: 20px;
    }

    &__current-dir {
        color: #333;
    }

    &__list {
        display: flex;
        flex-direction: row;
        gap: 20px;

        margin-top: 60px;
        padding: 0;

        list-style-type: none;
    }

    &__previous-dir {
        color: #888;
        text-decoration: underline;
    }

    &__previous-dir:hover {
        color: #B41818;
        text-decoration: none;
    }
}